:root{
  scroll-behavior: smooth;
}
body {
  display: flex;
  align-items: flex-start;
  justify-content: center;
  
  min-height: 100vh;
  width: 100vw;
  
  margin: 0;
  padding: 20px;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F5F5;
  color: #121212;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.forms__form {

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  grid-gap: 8px;
  gap: 8px;

  -webkit-animation: appearFromTheBottom 0.7s ease-out;

          animation: appearFromTheBottom 0.7s ease-out;
}
@-webkit-keyframes appearFromTheBottom{
  0%{
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
    opacity: 0;
  }
  50%{
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
@keyframes appearFromTheBottom{
  0%{
    -webkit-transform: translateY(40%);
            transform: translateY(40%);
    opacity: 0;
  }
  50%{
    opacity: 0;
  }
  100%{
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}
.form__button-back {
  background-color: #f5f5f5;
  border: 2px solid #121212;
  border-radius: 4px;
}
.form__button-back:active{
  border: 2px solid #898989;
}

.form__title {
  margin-top: 10px;
  
  font-weight: 500;
}
.form__input-group{
  display: flex;
  flex-direction: column;
  grid-gap: 3px;
  gap: 3px;
}
.input-group__label{
  max-width: 260px;

  color: #595959;
  font-size: 17px;
}
.input-group__input{
  width: 260px;

  padding: 7px 7px;
  box-sizing: border-box;

  border-color: transparent;
  border-bottom: 2px solid #121212;
  border-radius: 4px 4px 0 0;
  font-size: 16px;
}
.form__button-submit{
  display: flex;
  align-items: center;
  justify-content: space-around;

  min-width: 260px;

  padding: 8px;
  margin-top: 15px;

  background-color: #f5f5f5;
  border: 2px solid #121212;
  border-radius: 4px;
  color: #121212;
  font-size: 17px;
  font-weight: 500;
}
.form__button-submit:active{
  border: 2px solid #898989;
}
.App_header{
  -webkit-animation: appearFromTheBottom 0.7s linear;
          animation: appearFromTheBottom 0.7s linear;
}
.header__text-introduction{
  color: #595959;
}
.header__button{
  display: flex;
  align-items: center;
  justify-content: space-around;

  min-width: 200px;

  padding: 6px;

  background-color: #F5F5F5;
  border: 2px solid #121212;
  border-radius: 4px;
  color: #121212;
  font-size: 17px;
  font-weight: 500;
}
.header__button:active{
  border: 2px solid #898989;
}
.App__show-cv{
  -webkit-animation: appearFromTheBottom 0.7s ease-out;
          animation: appearFromTheBottom 0.7s ease-out;
}
.disappearToTheTop{
  -webkit-animation: disappearToTheTop 0.5s ease-in;
          animation: disappearToTheTop 0.5s ease-in;
}
@-webkit-keyframes disappearToTheTop {
  0%{
    opacity: 1;
  }
  50%{
    opacity: 1;
  }
  100%{
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
  }
}
@keyframes disappearToTheTop {
  0%{
    opacity: 1;
  }
  50%{
    opacity: 1;
  }
  100%{
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    opacity: 0;
  }
}
.info-section__info-principal {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
}
.info-principal__image {
  display: flex;
  align-items: center;
  justify-content: center;

  width: 200px;
  height: 240px;

  background-color: #595959;
  color: #f5f5f5;
}
.info-principal__title {
  font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
  font-size: 32px;
  font-style: italic;
  font-weight: 500;
}
.info-section__title{
  color: #414141;
  font-weight: 500;
  font-size: 26px;
}
.info-section__text{
  color: #595959;
}
.show-cv__buttons {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  grid-gap: 10px;
  gap: 10px;
}

.show-cv__button {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5px;
  gap: 5px;

  padding: 7px;

  background-color: #f5f5f5;
  border: 2px solid #121212;
  border-radius: 4px;
  color: #121212;
  font-size: 17px;
  font-weight: 500;
}
@media print {
  .show-cv__buttons {
    display: none;
  }
  .info-principal__title {
    max-width: 50%;
  }
}

