.forms__form {

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 8px;

  animation: appearFromTheBottom 0.7s ease-out;
}
@keyframes appearFromTheBottom{
  0%{
    transform: translateY(40%);
    opacity: 0;
  }
  50%{
    opacity: 0;
  }
  100%{
    transform: translateY(0);
    opacity: 1;
  }
}
.form__button-back {
  background-color: #f5f5f5;
  border: 2px solid #121212;
  border-radius: 4px;
}
.form__button-back:active{
  border: 2px solid #898989;
}

.form__title {
  margin-top: 10px;
  
  font-weight: 500;
}
.form__input-group{
  display: flex;
  flex-direction: column;
  gap: 3px;
}
.input-group__label{
  max-width: 260px;

  color: #595959;
  font-size: 17px;
}
.input-group__input{
  width: 260px;

  padding: 7px 7px;
  box-sizing: border-box;

  border-color: transparent;
  border-bottom: 2px solid #121212;
  border-radius: 4px 4px 0 0;
  font-size: 16px;
}
.form__button-submit{
  display: flex;
  align-items: center;
  justify-content: space-around;

  min-width: 260px;

  padding: 8px;
  margin-top: 15px;

  background-color: #f5f5f5;
  border: 2px solid #121212;
  border-radius: 4px;
  color: #121212;
  font-size: 17px;
  font-weight: 500;
}
.form__button-submit:active{
  border: 2px solid #898989;
}